#root {
  position: absolute;
  top: 0;
  width: 100%;
  min-height: 1vh;
}

@-webkit-keyframes slideUp {
  0%    {-webkit-transform: translateY(63px) scale(0.99);transform: translateY(63px) scale(0.99)};
  50%   {-webkit-transform:  scale(0.2);transform:  scale(0.2)};
  100%  {-webkit-transform: translateY(0px) scale(1.0);transform: translateY(0px) scale(1.0)};  
}

@keyframes slideUp {
  0%    {-webkit-transform: translateY(63px) scale(0.99);transform: translateY(63px) scale(0.99)};
  50%   {-webkit-transform:  scale(0.2);transform:  scale(0.2)};
  100%  {-webkit-transform: translateY(0px) scale(1.0);transform: translateY(0px) scale(1.0)};  
}

@-webkit-keyframes slideDown {
  0%    {-webkit-transform: translateY(-63px) scale(1.01);transform: translateY(-63px) scale(1.01)};
  50%   {-webkit-transform:  scale(1.4);transform:  scale(1.4)};
  100%  {-webkit-transform: translateY(0px) scale(1.0);transform: translateY(0px) scale(1.0)};
}

@keyframes slideDown {
  0%    {-webkit-transform: translateY(-63px) scale(1.01);transform: translateY(-63px) scale(1.01)};
  50%   {-webkit-transform:  scale(1.4);transform:  scale(1.4)};
  100%  {-webkit-transform: translateY(0px) scale(1.0);transform: translateY(0px) scale(1.0)};
}

.slideUp {
  -webkit-animation: slideUp 600ms;
          animation: slideUp 600ms;
}

.slideDown {
  -webkit-animation: slideDown 600ms;
          animation: slideDown 600ms;
}

